@import "./style/_vars.scss"; @import "./style/_mixins.scss";
@mixin active-input() {
  ~ label {
    @include label-medium();
    top: -7px;
    left: 23px;
  }
}

:root {
  --ts-pr-clear-button: 0;
  --ts-pr-caret: 0;
  --ts-pr-min: .75rem;
}

.ts-wrapper.single .ts-control, .ts-wrapper.single .ts-control input {
  cursor: pointer;
}

.ts-control {
